<template>
  <div class="NewAccount">
    <b-form @submit="onSubmit" @reset="onReset" v-if="show">
      <b-form-group id="grpFirstName" label="First Name:" label-for="firstName">
        <b-form-input id="firstName" type="text" v-model="form.firstName" required placeholder="Enter first name">
        </b-form-input>
      </b-form-group>
      <b-form-group id="grpLastName" label="Last Name:" label-for="lastName">
        <b-form-input id="lastName" type="text" v-model="form.lastName" required placeholder="Enter last name">
        </b-form-input>
      </b-form-group>
      <b-form-group id="grpAccountNo" label="Account Number:" label-for="accountNo">
        <b-form-input id="accountNo" type="text" v-model="form.accountNo" required placeholder="Enter account no.">
        </b-form-input>
      </b-form-group>
      <b-button type="submit" variant="primary">Submit</b-button>
      <b-button type="reset" variant="danger">Reset</b-button>
    </b-form>
  </div>
</template>

<script>
  import axios from 'axios'
export default {
  name: 'NewAccount',
  data () {
    return {
      title: 'Create a new account',
      form: {
        firstName: '',
        lastName: '',
        accountNo: ''
      },
      show: true
    }
  },
  methods: {
    	onSubmit (evt) {
    		const that = this;
    		axios({
          method: 'post',
          url: 'http://localhost:8088/accounts/create',
          data: {
            firstName: that.form.firstName,
            lastName: that.form.lastName,
            accountNumber: that.form.accountNo
          },
        }).then(response => console.log(JSON.stringify(response))).catch(e => console.log(e))
    	},
    	onReset (evt) {
    		evt.preventDefault()
    		this.form.firstName = ''
    		this.form.lastName = ''
    		this.form.accountNo = ''
    		this.show = false
    	}
  }
}
</script>



